import * as React from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import "./selectedtextfields.css"
import { height } from '@mui/system';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import DeleteIcon from '@mui/icons-material/Delete';
import AddBoxIcon from '@mui/icons-material/AddBox';
import ArrowCircleDownIcon from '@mui/icons-material/ArrowCircleDown';
import SearchIcon from '@mui/icons-material/Search';


const currencies = [
  {
    value: 'USD',
    label: '$',
  },
  {
    value: 'EUR',
    label: '€',
  },
  {
    value: 'BTC',
    label: '฿',
  },
  {
    value: 'JPY',
    label: '¥',
  },
];

export default function SelectTextFields() {
  const [currency, setCurrency] = React.useState('EUR');

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setCurrency(event.target.value);
  };

  return (
     

    <Stack direction="row"  justifyContent="space-between" alignContent="center">
        
       <Stack spacing={2} direction="row" alignItems="center">
       {/* <TextField
          id="outlined-select-currency"
          select
         
          value={currency}
          onChange={handleChange}
          sx={{height:10,width:70}}
        >
          {currencies.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}
        </TextField> */}
        <input variant="outlined" style={{margin:"10px" }}></input>
       </Stack>

     
      <Stack spacing={2} direction="row" alignItems="center">
      <Button variant="contained" style={{textTransform:"capitalize",fontSize:"12"}}> <DeleteIcon/> &nbsp; Batch Delete</Button>
      <Button variant="contained" style={{textTransform:"capitalize",fontSize:"12"}}> <ArrowCircleDownIcon/> &nbsp;Import products</Button>
      <Button variant="contained" style={{textTransform:"capitalize",fontSize:"12"}}>  <AddBoxIcon/> &nbsp; Add products</Button>
  
       </Stack>
        
        {/* <label>Record</label> */}
        
   
       
        
      
          
       
      
           
             
        
       
   

   
    

    
    

    
    
        
      </Stack>
    
   
      
      

      
    
    
    
     
    // </Box>
    
    
    

     
   
     

 
    
  );
}
