import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import "./datatable.css"
import Switch from '@mui/material/Switch';
import IconButton from '@mui/material/IconButton';
import Stack from '@mui/material/Stack';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import {blue } from '@mui/material/colors';
import {red } from '@mui/material/colors';



function createData(name, calories, fat, carbs, protein, pro, img="https://www.freepik.com/free-photos-vectors/box-mockup") {
  return { name, calories, fat, carbs, protein, pro, carbs};
}

const rows = [
  createData('6868G-M60', '6868G-M60','simple','$5.00','$0.00','12th april','button'),
  createData('6868G-M60', '6868G-M60','simple','$5.00','$0.00','12th april','button'),
  createData('6868G-M60', '6868G-M60','simple','$5.00','$0.00','12th april','button'),
  createData('6868G-M60', '6868G-M60','simple','$5.00','$0.00','12th april','button'),
  createData('6868G-M60', '6868G-M60','simple','$5.00','$0.00','15th april','button'),
  createData('6868G-M60', '6868G-M60','simple','$5.00','$0.00','15th april','button'),
];

export default function DataTable() {
  return (
    <TableContainer component={Paper} className="display">
      <Table sx={{ minWidth: 350 }}>
        <TableHead>
          <TableRow >
            <TableCell align="center" style={{fontWeight:'bold', fontSize:'13px',fontFamily:"Open Sans"}}>SKU</TableCell>
            <TableCell align="center" style={{fontWeight:'bold', fontSize:'13px',fontFamily:"Open Sans"}}>PRODUCT NAME</TableCell>
            <TableCell align="center" style={{fontWeight:'bold', fontSize:'13px',fontFamily:"Open Sans"}}>TYPE</TableCell>
            <TableCell align="center" style={{fontWeight:'bold', fontSize:'13px',fontFamily:"Open Sans"}}>AVERAGE COST</TableCell>
            <TableCell align="center" style={{fontWeight:'bold', fontSize:'13px',fontFamily:"Open Sans"}}>PRICE</TableCell>
            <TableCell align="center" style={{fontWeight:'bold', fontSize:'13px',fontFamily:"Open Sans"}}>PRODUCT<br></br>STATUS</TableCell>
            <TableCell align="center" style={{fontWeight:'bold', fontSize:'13px',fontFamily:"Open Sans"}}>LAST <br></br>UPDATED</TableCell>
            <TableCell align="center" style={{fontWeight:'bold', fontSize:'13px',fontFamily:"Open Sans"}}>ACTION</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.name}
              sx={{ '&:first-child td, &:first-child th': { border: 0} }}
            >
              
              {/* <div className='cellWrapper'>
                    <img src='https://www.freepik.com/free-photos-vectors/box-mockup' className='image'/> &nbsp;6868G-M60
                </div> */}
              <TableCell align="center" style={{color:"primary",fontFamily:"Open Sans"}}>{row.calories}</TableCell>
              <TableCell align="center" style={{fontFamily:"Open Sans"}}>{row.calories}</TableCell>
              <TableCell align="center" style={{fontFamily:"Open Sans"}}>{row.fat}</TableCell>
              <TableCell align="center" style={{fontFamily:"Open Sans"}}>{row.carbs}</TableCell>
              <TableCell align="center" style={{fontFamily:"Open Sans"}}>{row.protein}</TableCell>
              <div className='cellWrapper'>
              <Switch defaultChecked style={{align:'center'}}/>
             </div>
              <TableCell align="center" style={{fontFamily:"Open Sans"}}>{row.pro}</TableCell>
                 
              
                <IconButton aria-label="delete" style={{ justifyContent:'center'}}>
                 <EditIcon sx={{fontSize: 18, color:  blue [500] }}  />
              </IconButton>
              <IconButton aria-label="delete">
                 <DeleteIcon sx={{fontSize: 18, color:  red [500] }} />
              </IconButton>
              
      

              
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
